<template>
  <div class="row">
    <CoolLightBox
      :items="images"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>
    <div class="flex-x-center" style="padding: 0px 10px; margin-top: 30px">
      <!-- <h4 class="mb-0">Yiu's Villa Supplier</h4> -->
      <img src="/logo.png" width="150px" alt="" />
      <p class="mb-0">
        {{ order.date }}
      </p>
    </div>
    <div class="content" style="margin-bottom: 30px">
      <div class="card-product">
        <h5 style="margin: 15px 0px">Customer</h5>
        <p class="gray-bg">Order ID : {{ order.id }}</p>

        <p class="gray-bg">Name : {{ order.customer_name }}</p>
        <p class="gray-bg">
          Phone :
          {{ order.phone }}
        </p>
        <p class="gray-bg" v-if="order.address">
          Address :{{ order.address.address }},
          {{ order.address.city.split("_").join(" ") }},
          {{ order.address.state.split("_").join(" ") }}
        </p>
      </div>
      <div class="card-product">
        <h5 style="margin: 15px 0px">Items</h5>
        <div
          class="gray-bg item-row"
          style="position: relative"
          v-for="(product, index) in order.products"
          :key="index"
        >
          <img
            :src="product.image"
            class="item-img"
            @click="viewImage(product.image)"
            alt="Image"
            @load="loaded"
          />
          <div style="width: calc(100% - 100px); padding: 9px">
            <p
              style="
                font-size: 15px !important;
                display: flex;
                align-items: center;
              "
            >
              <span class="number">
                {{ index + 1 }}
              </span>
              {{ product.name || "-" }}
            </p>
            <a
              :href="getLink(product.link)"
              v-if="product.link"
              target="_blank"
              class="sm-text code"
              style="margin-bottom: 8px; display: block"
              rel="noopener noreferrer"
            >
              {{ product.link }}

              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="14"
                height="14"
                style="position: relative; top: 2px"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
                class="feather feather-external-link"
              >
                <path
                  d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6"
                ></path>
                <polyline points="15 3 21 3 21 9"></polyline>
                <line x1="10" y1="14" x2="21" y2="3"></line>
              </svg>
            </a>
            <p v-else class="xs-text">No tracking code yet</p>
            <div
              style="
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                width: 100%;
              "
            >
              <p style="margin-bottom: 0px !important; color: #6951a4">
                {{ product.quantity }}x{{ product.sale_price }}¥
              </p>
              <p
                style="
                  font-weight: bold;
                  font-size: 18px !important;
                  color: #6951a4;
                  margin-bottom: 0px !important;
                "
              >
                {{ parseFloat(product.subtotal).toFixed(2) }}¥
              </p>
            </div>
          </div>
          <img
            v-if="product.quantity === product.number_of_fullfilled"
            src="/done.png"
            width="80px"
            class="done-img"
            alt=""
          />
          <!-- <td>{{ product.quantity }}x{{ product.sale_price }}</td>
            <td>
              {{ parseFloat(product.subtotal).toFixed(2) }}
            </td> -->
        </div>
      </div>
      <!-- <div class="card-product">
        <h5 style="margin: 15px 0px">Items</h5>
        <table id="products">
          <tr>
            <th>No.</th>
            <th>Item</th>
            <th>Rate</th>
            <th>Subtotal</th>
          </tr>
          <tr v-for="(product, index) in order.products" :key="index">
            <td>
              {{ index + 1 }}
            </td>
            <td class="item-row">
              <img
                :src="product.image"
                class="item-img"
                @click="viewImage(product.image)"
                alt="Image"
                @load="loaded"
              />
              <div>
                <p>{{ product.name || "-" }}</p>
                <a
                  :href="getLink(product.link)"
                  v-if="product.link"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {{ product.link }}
                </a>
              </div>
            </td>

            <td>{{ product.quantity }}x{{ product.sale_price }}</td>
            <td>
              {{ parseFloat(product.subtotal).toFixed(2) }}
            </td>
          </tr>
        </table>
      </div> -->
      <div class="card-product">
        <h5 style="margin: 15px 0px">
          Total price of {{ getItemsQuantity(order) }} Items
        </h5>
        <p class="gray-bg flex-between">
          <span> Subtotal</span>
          <span class="primary">{{ order.subtotal }}¥</span>
        </p>
        <p class="gray-bg flex-between">
          <span> Percentage({{ order.supplier_percentage }}%)</span>
          <span class="primary">
            {{
              (
                parseFloat(order.subtotal) *
                (parseFloat(order.supplier_percentage) / 100)
              ).toFixed(2)
            }}¥</span
          >
        </p>

        <div class="gray-bg flex-between flex-start">
          <div>
            <span> Total</span><br />
            <span class="xs-text">
              ({{
                order.subtotal +
                "¥ + " +
                (
                  parseFloat(order.subtotal) *
                  (parseFloat(order.supplier_percentage) / 100)
                ).toFixed(2) +
                "¥"
              }})
            </span>
          </div>
          <span class="primary"> {{ order.total }}¥</span>
        </div>

        <div class="gray-bg flex-between flex-start">
          <div>
            <span> Total in MMK </span><br />
            <span class="xs-text">
              ({{ order.total + "¥ x " + order.money_price + "MMk" }})
            </span>
          </div>

          <span class="primary">
            {{ parseInt(order.total) * parseInt(order.money_price) }} MMK</span
          >
        </div>
        <button class="export" v-on:click="onBtExport()">
          Export to Excel
        </button>
        <ag-grid-vue
          style="width: 300px; height: 300px"
          class="ag-theme-alpine"
          :columnDefs="columnDefs"
          @grid-ready="onGridReady"
          :defaultColDef="defaultColDef"
          :defaultExcelExportParams="defaultExcelExportParams"
          :context="context"
        ></ag-grid-vue>
      </div>
      <ImageRenderCell />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import couriors from "./couriors";

import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-alpine.css";
import "ag-grid-enterprise";
import { AgGridVue } from "ag-grid-vue";
import ImageRenderCell from "./ImageRenderCell.js";
import { createBase64FromResponse } from "./imageUlti";
const indexCode = {};

const base64Img = {};

export default {
  data() {
    return {
      order: {
        products: [],
      },
      loading_: null,
      images: [],
      index: null,
      openImage: false,
      gettingSale: true,
      columnDefs: [
        {
          field: "item",
          headerName: "Item",
          minWidth: 150,
          width: 150,
          maxWidth: 150,
          cellRenderer: "ImageRenderCell",
          cellRendererParams: {
            base64Img: base64Img,
            indexCode: indexCode,
          },
        },

        { field: "rate" },
        { field: "subtotal" },
      ],
      gridApi: null,
      columnApi: null,
      defaultColDef: {
        width: 150,
        resizable: true,
      },
      defaultExcelExportParams: null,
      context: null,
    };
  },
  created() {
    const queries = new URLSearchParams(window.location.search);
    const sid = queries.get("sid");
    const oid = queries.get("oid");
    const date = queries.get("date");

    this.getOrder(sid, oid, date);

    this.defaultExcelExportParams = {
      addImageToCell: (rowIndex, col, value) => {
        console.log("🚀 ~ file: App.vue ~ line 299 ~ created ~ value", value);
        if (col.getColId() !== "item") {
          return;
        }
        console.log(
          "🚀 ~ file: App.vue ~ line 315 ~ created ~ base64Img",
          base64Img
        );

        return {
          image: {
            id: value,
            base64: base64Img[value],
            imageType: "png",
            width: 150,
            height: 150,
            position: {
              offsetX: 30,
              offsetY: 5.5,
            },
          },
        };
      },
    };
    this.context = {
      base64Img: base64Img,
    };
  },
  components: {
    "ag-grid-vue": AgGridVue,
    ImageRenderCell,
  },
  methods: {
    loaded(e) {
      e.path[0].style.height = e.path[0].clientWidth + "px";
    },
    onBtExport() {
      this.gridApi.exportDataAsExcel();
    },
    onGridReady(params) {
      this.gridApi = params.api;
      this.gridColumnApi = params.columnApi;
      const queries = new URLSearchParams(window.location.search);

      const sid = queries.get("sid");
      const oid = queries.get("oid");
      const date = queries.get("date");
      fetch(
        `https://osassist.pythonanywhere.com/api/sale-view/?sid=${sid}&oid=${oid}&date=${date}`
      )
        .then((data) => {
          return createBase64FromResponse(data, base64Img);
        })
        .then((data) => {
          const value = data.map((product) => {
            return {
              item: product.image,
              rate: product.quantity + "x" + product.sale_price + "¥",
              subtotal: product.subtotal + "¥",
            };
          });
          params.api.setRowData(value);
        });
    },
    viewImage(img) {
      this.images = [img];
      this.index = 0;
    },
    getItemsQuantity(sale) {
      return sale.products.reduce((total, item) => {
        return total + parseFloat(item.quantity);
      }, 0);
    },
    getLink(code) {
      var link = "";
      couriors.forEach((cr) => {
        cr.prefix.forEach((prefix) => {
          if (code.startsWith(prefix)) {
            link = cr.link + code;
          }
          console.log(
            "🚀 ~ file: App.vue ~ line 135 ~ couriors.forEach ~ code.startsWith(prefix)",
            code.startsWith(prefix)
          );
        });
      });
      return link;
    },
    getOrder(sid, oid, date) {
      var link = `https://osassist.pythonanywhere.com/api/sale-view/?sid=${sid}&oid=${oid}&date=${date}`;
      axios
        .get(link)
        .then((response) => {
          this.order = response.data;
          response.data.products.forEach((item) => {
            var img = new Image();
            img.src = item.image;
          });
          const loader = document.querySelector(".page-loader");
          loader.className += " hidden";
          setTimeout(() => {
            loader.className += " display_none";
          }, 1000);
        })
        .catch(() => {
          alert("Error!");
        });
    },
  },
};
</script>

    <style >
