export const createBlob = (response) => response.blob();

export const fromBlobToBase64 = (blob) =>
  new Promise((res) => {
    const reader = new FileReader();
    reader.onloadend = () => res(reader.result);
    reader.readAsDataURL(blob);
  });

  export const createBase64ImageFromURL = (url) =>
  fetch(url)
    .then(createBlob)
    .then((blob) => fromBlobToBase64(blob));

    export const createBase64ImagesFromURLArray = (arr) => {
  const map = new Map();
  const promiseArray = arr.reduce((promises, currentUrl) => {
    if (!map.has(currentUrl)) {
      const promise = createBase64ImageFromURL(currentUrl);
      promise.then((base64) => map.set(currentUrl, base64));
      promises.push(promise);
    }

    return promises;
  }, []);

  return Promise.all(promiseArray).then(() => map);
};

// Example specific code
export const createCountryCodeMap = (countryCodeObject) =>
  fetch('https://flagcdn.com/en/codes.json')
    .then((response) => response.json())
    .then((codes) => {
      Object.keys(codes).forEach(
        (code) => (countryCodeObject[codes[code]] = code)
      );
      return countryCodeObject;
    });

export const createBase64FromResponse = (
  response,
  base64Object
) => {
 
  return response
    .json()
    .then((data) => {
      const urls = data.products.map((rec) => rec.image);
      return createBase64ImagesFromURLArray(urls).then((map) => {
        map.forEach((value, key) => {
          const code = key;
          base64Object[code] = value;
        });

        return data;

      });
    })
    .then((data) => data.products);
};